.hero-content-section {
  padding-top: 50px;
}

.hero-content-wrapper {
  flex-direction: column;
  justify-content: center;
  max-width: 320px;
  margin: 0 auto;
  display: flex;
  position: relative;
}

.hero-content-title {
  font-size: var(--font-size-medium);
  letter-spacing: 1.04px;
  max-width: 247px;
}

.hero-content-title-highlighted {
  font-size: var(--font-size-heading-highlight);
  line-height: 0;
}

.hero-button-container {
  justify-content: space-between;
  align-items: center;
  gap: 12px;
  max-width: 220px;
  padding-top: 28px;
  padding-bottom: 42px;
  padding-left: 20px;
  display: flex;
}

@keyframes plateSpin {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

.hero-content-ice-spinning {
  animation: 20s linear infinite plateSpin;
}

.hero-content-description {
  display: none;
}

.hero-content-button-border {
  color: var(--black-shade);
  font-size: var(--font-size-xs);
  border: solid 2px var(--yellow);
  border-radius: 8px;
  padding: 12px 34px;
  font-weight: 600;
  text-decoration: none;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
}

.hero-content-button-border:hover, .hero-content-button-border:focus {
  border: solid 2px var(--dark-green);
}

.hero-content-button-underline {
  color: var(--yellow);
  font-size: var(--font-size-xs);
  white-space: nowrap;
  font-weight: 600;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
}

.hero-content-button-underline:hover, .hero-content-button-underline:focus {
  color: var(--dark-green);
}

.hero-content-text {
  color: var(--white);
  font-size: var(--font-size-small);
  letter-spacing: normal;
  font-family: DM Sans, sans-serif;
  line-height: 1.4;
}

.hero-content-arrow-icon {
  fill: var(--white);
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
}

.hero-content-arrow-icon:hover, .hero-content-arrow-icon:focus {
  transform-origin: 0;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  transform: scaleX(1.2);
}

.hero-content-image-wrapper {
  justify-content: center;
  align-items: center;
  display: flex;
  position: relative;
}

.hero-content-dots-mobile-left {
  z-index: -1;
  position: absolute;
  bottom: 0;
  left: 0;
}

.hero-content-dots-mobile-right {
  z-index: -1;
  position: absolute;
  bottom: 0;
  left: auto;
  right: 0;
}

.hero-content-dots-480, .hero-content-dots-tablet-l, .hero-content-dots-tablet-r, .hero-content-dots-desktop {
  display: none;
}

@keyframes pulseShadow {
  0% {
    filter: drop-shadow(0 0 100px #ffffff4d);
  }

  50% {
    filter: drop-shadow(0 0 40px #fff9);
  }

  100% {
    filter: drop-shadow(0 0 100px #ffffff4d);
  }
}

@keyframes onPageLoad {
  0% {
    transform: rotate(0)scale(.1);
  }

  100% {
    transform: rotate(360deg)scale(1);
  }
}

.hero-content-image {
  animation-name: pulseShadow, onPageLoad;
  animation-duration: 2s, .5s;
  animation-timing-function: ease-in-out, linear;
  animation-iteration-count: infinite, 1;
  animation-direction: normal, normal;
  animation-play-state: running, running;
  animation-delay: 0s, 0s;
  animation-fill-mode: forwards;
  display: inline-block;
}

.hero-content-image-tablet, .hero-content-statistics-container {
  display: none;
}

.hero-content-statistics-number {
  font-size: var(--font-size-xxl-small);
  text-align: right;
  font-weight: 900;
}

.hero-content-statistics-number:first-of-type:after {
  content: "cafes";
}

.hero-content-statistics-number:nth-of-type(2):after {
  content: "food trucks";
}

.hero-content-statistics-number:after {
  color: var(--white-shade);
  font-size: var(--font-size-small);
  padding-top: 4px;
  font-weight: 400;
  display: block;
}

.hero-content-dots {
  z-index: -1;
  position: absolute;
  top: 155px;
  left: 0;
}

@media screen and (width >= 480px) {
  .hero-content-section {
    padding-top: 40px;
    padding-left: 20px;
    padding-right: 20px;
    position: relative;
  }

  .hero-content-wrapper {
    max-width: 480px;
    bottom: 0;
  }

  .hero-button-container {
    padding-bottom: 150px;
  }

  .hero-content-image-wrapper {
    position: absolute;
    bottom: 0;
    right: 0;
  }

  .hero-content-dots-mobile-left {
    left: -80px;
  }

  .hero-content-dots-mobile-right {
    right: 0;
  }

  .hero-content-dots-480 {
    display: block;
    position: absolute;
    bottom: 0;
    left: 0;
  }
}

@media screen and (width >= 768px) {
  .hero-content-wrapper {
    max-width: 768px;
  }

  .hero-content-section {
    padding: 14px 32px 0;
  }

  .hero-content-title {
    font-size: var(--font-size-medium);
    max-width: 247px;
    padding-left: 0;
  }

  .hero-button-container {
    padding-bottom: 12px;
    padding-left: 0;
  }

  .hero-content-description {
    justify-content: flex-start;
    gap: 16px;
    max-width: 441px;
    display: flex;
  }

  .hero-content-text-wrapper {
    flex-direction: column;
    justify-content: flex-end;
    max-width: 200px;
    padding-bottom: 24px;
    display: flex;
  }

  .hero-content-description {
    max-width: 349px;
  }

  .hero-content-text {
    font-size: var(--font-size-xs);
    padding-bottom: 29px;
  }

  .hero-content-ice-spinning {
    width: 152px;
    height: 152px;
  }

  .hero-content-image-wrapper {
    justify-content: flex-start;
    align-items: stretch;
    top: 30px;
    bottom: 0;
  }

  .hero-content-statistics-container {
    justify-content: flex-start;
  }

  .hero-content-dots-mobile-left, .hero-content-dots-mobile-right {
    display: none;
  }

  .hero-content-dots-tablet-l {
    z-index: -1;
    display: block;
    position: absolute;
    bottom: 0;
    left: -440px;
  }

  .hero-content-dots-tablet-r {
    z-index: -1;
    display: block;
    position: absolute;
    bottom: 0;
    right: 0;
  }

  .hero-content-statistics-container {
    flex-direction: column;
    gap: 37px;
    display: flex;
  }

  .hero-content-statistics-number {
    font-size: var(--font-size-heading-xs);
  }

  .hero-content-statistics-number:after {
    font-size: var(--font-size-xxs);
  }
}

@media screen and (width >= 1200px) {
  .hero-content-section {
    padding-top: 45px;
  }

  .hero-content-wrapper {
    max-width: 1200px;
  }

  .hero-content-description {
    max-width: 475px;
  }

  .hero-content-ice-spinning {
    width: 255px;
    height: 255px;
  }

  .hero-content-title {
    font-size: var(--font-size-xl);
    max-width: 367px;
  }

  .hero-content-text-wrapper {
    justify-content: center;
    max-width: 200px;
  }

  .hero-button-container {
    padding-bottom: 42px;
  }

  .hero-content-text {
    font-size: var(--font-size-small);
  }

  .hero-content-dots-tablet-l, .hero-content-dots-tablet-r {
    display: none;
  }

  .hero-content-dots-desktop {
    z-index: -1;
    display: block;
    position: absolute;
    bottom: 0;
    right: 0;
  }

  .hero-content-statistics-number {
    font-size: var(--font-size-xxl-small);
  }

  .hero-content-statistics-number:after, .hero-content-button-border, .hero-content-button-underline {
    font-size: var(--font-size-small);
  }
}
